const { SV } = process.env
const root = 'https://widget.arenaspace.ru'

const path = url => `${root}/${url.replace(/\.(\w+)$/, `.${SV}.$1`)}`

const attach = el => document.getElementsByTagName('head')[0].appendChild(el)

const loadJS = url => {
  const script = document.createElement('script')
  script.async = true
  script.setAttribute('type','text/javascript')
  script.setAttribute('src', path(url))
  attach(script)
}

const loadCSS = url => {
  const link = document.createElement('link')
  link.rel = 'stylesheet'
  link.type = 'text/css'
  link.href = path(url)
  link.media = 'all'
  attach(link)
}

loadJS('loader.js')
loadCSS('loader.css')